import React, { useState } from "react";
import { subscribe } from "../../services/subscribe";

const Subscribtion = () => {
  const [email, setEmail] = useState('');
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  const subscribeAction = async () => {
    try {
      if (email === '') {
        setError('Please fill all the fields');
        return;
      }

      if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)) {
        setError('Please enter valid email');
        return;
      }
      const response = await subscribe({ email: email });

      if (response.data.id) {
        setSuccess("You have successfully subscribed");
      }

      setEmail('');
    } catch (err) {
      setError("User already subscribed !");
      setEmail(email)
    }
  };

  setTimeout(() => {
    setError('');
    setSuccess('');
  }, 10000);

  const updateForm = (e) => {
    const { value } = e.target;
      setEmail(value.toLowerCase());
};

  return (
    <section id="waitlist" className="overflow-hidden"
    >
      <div className="container" style={{ marginTop: '200px' }}>
        <div className="text-center col-md-12">
          <button style={{ borderRadius: "7px" }} className="btn btn-coming-soon">
            COMING SOON
          </button>
          <div className="header-title">
          <h3 style={{ width: "100%", }} className="heading mx-auto mb-2" >The intelligent way to manage employees</h3>
          </div>
          <p className="mx-auto " style={{ fontWeight: "200", width: "70%", fontSize: '1rem' }}>
            Get access to effectively manage your business, resources and staff at your comfort . Less stress, More productivity.
          </p>
        </div>
        <div className="row height-100 d-flex justify-content-center align-items-center ">
          <div className="col-md-6">
            {error && <div className="alert alert-warning" role="alert">{error}</div>}
            {success && <div className="alert alert-success" role="alert">{success}</div>}
            <div className="search position-relative">

              <input
                style={{ width: '100%' }}
                name="email"
                value={email}
                className="form-control subscribe-input"
                placeholder="Email goes here..."
                onChange={updateForm}
              />
              <button onClick={() => subscribeAction()} className="btn btn-hrmaneja position-absolute">
                Join waitlist
              </button>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Subscribtion;
